.Event {
  border-radius: 10px;
  position: relative;
}

.Event:hover .Event__hint {
  display: block;
}

.Event__title {
  padding: 5px 10px;
  color: #fff;
  margin-bottom: 3px;
  font-size: 20px;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

.Event__line {
  max-width: 240px;
  width: 100%;
  height: 1px;
  background: #fff;
  margin-bottom: 3px;
}

.Event__hint {
  display: none;
  background: #00000080;
  padding: 10px;
  border-radius: 10px;
  color: #fff;
  text-align: center;
  width: 250px;
  position: absolute;
  bottom: -100%;
  z-index: 1;
}

.Event__icon {
  width: 90%;
  height: 90%;
  margin: 0 auto;
  background: url("./img/Event.png") no-repeat center / contain;
}
