.EventsForm__form {
  position: fixed;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.EventsForm__row {
  display: flex;
  align-items: center;
  margin: 10px;
}

.EventsForm__label {
  font-size: 20px;
  margin-right: 10px;
  width: 150px;
}

.EventsForm__input {
  padding: 15px;
}

.EventsForm__button {
  padding: 10px 25px;
  color: #fff;
  background: tomato;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  display: block;
  margin: 0 auto;
}
