.EventsCalendar {
  position: relative;
  overflow-x: auto;
  padding-bottom: 50px;
}

.EventsCalendar__grid {
  width: 1440px;
  display: grid;
  grid-template-columns: repeat(720, 4px);
  /* grid-template-rows: repeat(auto, 2px); */
}

.EventsCalendar__timeline {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.EventsCalendar__timeline-item {
  min-width: 120px;
  height: 100%;
  border-left: 1px solid #ccc;
  padding-left: 10px;
}
